	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"Functional Reactive Programming (FRP) is becoming an increasingly common way to structure event- driven code, but the term \"FRP\" actually refers to a large body of increasingly diverse work. This talk will quickly cover the basics of FRP, and then go into a couple different formulations of FRP that people are beginning to use. We will explore how these formulations fit together historically and theoretically.\nFinally, we will see how the particular variation of FRP used in Elm makes it easy to implement hot- swapping and Elm's Time Traveling Debugger.\n\nby Evan Czaplicki (@czaplic) - Prezi \n\nI am a functional programmer at heart, and my broad goal is to provide the tools and learning resources to make functional languages common in industry. I develop Elm 's compiler, tools, and community to make it great for projects within Prezi and in industry in general.","meta":{"duration":"PT40M55S"},"presenter":{"name":null},"source":{"id":"Agu6jipKfYw","provider":"youtube"},"slug":"controlling-time-and-space-understanding-the-many-formulations-of-frp-by-evan-czaplicki","tag":["Functional Reactive Programming","Elm","User Interface (Speech Topic)"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/Agu6jipKfYw/default.jpg","height":90,"width":120}},"title":"\"Controlling Time and Space: understanding the many formulations of FRP\" by Evan Czaplicki"}},{"node":{"description":"Curry On Prague, July 7th 2015\nhttp://curry-on.org\nhttp://2015.ecoop.org","meta":{"duration":"PT58M24S"},"presenter":{"name":null},"source":{"id":"oYk8CKH7OhE","provider":"youtube"},"slug":"evan-czaplicki-lets-be-mainstream-user-focused-design-in-elm-curry-on","tag":["Programming Language","Programmer (Profession)","Conference","Curry On","Prague (City/Town/Village)","Elm Programming Language","Front-End","Functional Reactive Programming","Language design"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/oYk8CKH7OhE/default.jpg","height":90,"width":120}},"title":"Evan Czaplicki - Let's be mainstream! User focused design in Elm - Curry On"}}]}},"pathContext":{"slug":"functional-reactive-programming","tag":"Functional Reactive Programming"}}

